import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Grid from "@material-ui/core/Grid";
import Typography from "../Typography";
import Box from "../Box";
import "../assets/css/slick-theme.css";

import { styles } from "./styles";

const Card = ({ classes, className, onClick, img, icon, title, subtitle }) => {
    return (
        <Box className={`${classes.root} ${className}`} onClick={onClick}>
            <Grid container>
                <Grid item xs={4}>
                    <span className={classes.imgContainerHighlight}>
                        <img className={classes.img} src={img} alt="" />
                    </span>
                </Grid>
                <Grid item xs={8} className={classes.container}>
                    <Typography type="headline5">
                        {icon && <span className={classes.icon}>{icon}</span>}
                        {title}
                    </Typography>
                    <Typography className={classes.subtitleHighlight} type="body1regular">
                        {subtitle}
                    </Typography>
                </Grid>
            </Grid>
        </Box>
    );
};

Card.propTypes = {
    classes: PropTypes.object.isRequired,
    imgs: PropTypes.array,
    onClick: PropTypes.func,
    icon: PropTypes.object,
    title: PropTypes.string,
    subtitle: PropTypes.string,
    className: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(Card);
